:root {
    --primary: #6200EE;
    --secondary: #03DAC6;
    --dark: #131a26;
    --light: #eee;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Fira-Sans', sans-serif;
}

body {
    background-color: #eee;
    background-color: var(--light);
}

.App {
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    padding: 16px;
}

.calculator {
    width: 100%;
    max-width: 400px;
    background-color: #FFF;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 2px 64px rgba(0, 0, 0, 0.2);
}

.display {
    padding: 16px;
    text-align: right;
    background-color: #131a26;
    background-color: var(--dark);
    color: #eee;
    color: var(--light);
    font-size: 24px;
    font-weight: 300;
}

.display span {
    font-size: 14px;
    color: #888;
}

button {
    -webkit-appearance: none;
            appearance: none;
    border: none;
    outline: none;
    color: #FFF;
    font-size: 20px;
    padding: 16px;
    cursor: pointer;
    transition: 0.4s;
}

button:hover {
    opacity: 0.9;
}

.operators{
    display: flex;
}

.operators button{
    flex: 1 1;
    background-color: #6200EE;
    background-color: var(--primary);
    font-weight: 700;
}

.digits{
    display: flex;
    flex-wrap: wrap;
}

.digits button{
    flex: 1 1 33.3333%;
    max-width: 33.3333%;
    background-color: #131a26;
    background-color: var(--dark);
}
